import { mapActions } from 'vuex';
export default {
  name: "getPathData",
  methods: {
    ...mapActions(["setCurrentStoreId"]),
    setStore(id) {
      if (this.$router.history.current.name === "ViewDeals") {
        this.setCurrentStoreId(null);
      } else {
        this.setCurrentStoreId(+id);
      }
    }
  },
  mounted() {
    const { setStore, id } = this;
    setStore(id);
  },
  watch: {
    id: function (newId, oldId) {
      if (oldId !== newId) {
        this.setStore(newId);
      }
    }
  },
  destroyed() {
    this.setCurrentStoreId(undefined);
  }
};
